body {
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  background-color: #212121;
  color: white;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  margin: auto;
}

.order-type-tabs {
  display: flex;
  align-items: center;
  padding: 4px;
  background-color: #3a3a3a;
  border-radius: 4px;
  width: 300px;
  margin-bottom: 24px;
}

.long {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  padding: 4px;
  background-color: #00e676;
  border-radius: 4px;
  color: white;
  opacity: 0.2;
}

.short {
  transition-duration: 0.3s;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  padding: 4px;
  background-color: #ff5252;
  border-radius: 4px;
  color: white;
  opacity: 0.2;
}

.active {
  opacity: 1;
}

.field-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;
}

.label {
  font-size: 12px;
  margin-bottom: 4px;
  color: #ffd54f;
}

.input-container {
  display: flex;
  align-items: center;
  width: 300px;
  padding: 8px;
  background-color: #3a3a3a;
  border-radius: 4px;
}

.input {
  transition-duration: 0.3s;
  width: 100%;
  color: #ffc107;
  margin-right: 8px;
  padding: 8px;
  outline: none;
  border: none;
  border-right: 1px solid #ffd54f;
  background-color: transparent;
  border-radius: 0px;
  font-size: 16px;
}

.input-suffix {
  display: flex;
  width: 50px;
  justify-content: center;
  color: #ffc107;
  font-weight: bold;
}

.result-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dollar-suffix {
  margin-left: 4px;
  display: flex;
  justify-content: center;
  color: #ffc107;
  font-weight: bold;
}

.error-box {
  width: 100%;
  transition-duration: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  padding: 12px 0;
  background-color: #ff5252;
  border-radius: 4px;
  margin-top: 8px;
  color: white;
}

.slider-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 300px;
  padding: 8px;
  background-color: #3a3a3a;
  border-radius: 4px;
}

.ticks {
  width: 284px;
  display: flex;
  justify-content: space-between;
  padding: 6px;
}

.slider {
  -webkit-appearance: none;
  box-sizing: content-box;
  appearance: none;
  width: 100%;
  background: #757575;
  box-shadow: inset 0 0 0 6px #3a3a3a;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  background: #f5f5f5;
  transform: rotate(45deg);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  background: #f5f5f5;
  transform: rotate(45deg);
  cursor: pointer;
}

/* For Firefox */

input[type="number"] {
  -moz-appearance: textfield;
}

/* Webkit browsers like Safari and Chrome */

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
